<template>
  <div>
    <router-link
      to="/"
      tag="div"
      class="header-abs"
      v-show="showAbs"
    >
      <div class="iconfont header-abs-back">&#xe624;</div>
    </router-link>
    <div
      class="header-fixed"
      v-show="!showAbs"
      :style="opacityStyle"
    >
      景点详情
      <router-link to="/">
        <div class="iconfont header-fixed-back">&#xe624;</div>
      </router-link>
    </div>
  </div>
</template>
<script>
export default {
  name: 'DetailHeader',
  data () {
    return {
      showAbs: true,
      opacityStyle: {
        opacity: 0
      }
    }
  },
  methods: {
    handlerScroll () {
      const top = document.documentElement.scrollTop
      console.log(top)
      if (top > 60) {
        let opacity = top / 140
        opacity = opacity > 1 ? 1 : opacity
        this.opacityStyle = { opacity }
        this.showAbs = false
      } else {
        this.showAbs = true
      }
    }
  },
  activated () {
    window.addEventListener('scroll', this.handlerScroll)
  },
  deactivated () {
    // 页面即将替换或隐藏是会执行
    window.removeEventListener('scroll', this.handlerScroll)
  }
}
</script>
<style scoped lang='stylus'>
@import '~styles/varibles.styl'
.header-abs
  width: .8rem
  height: .8rem
  line-height: .8rem
  border-radius: 50%
  position: absolute
  top: .2rem
  left: .2rem
  text-align: center
  background: rgba(0,0,0,0.8)
  .header-abs-back
    color: #fff
    font-size: .4rem
.header-fixed
  z-index:2
  position: fixed
  top:0
  left:0
  right:0
  height: $headerHeight
  line-height: $headerHeight
  text-align: center
  overflow: hidden
  color: #ffffff
  background: $bgColor
  font-size: 0.32rem
  .header-fixed-back
    width: 0.64rem
    text-align: center
    font-size: 0.4rem
    position:absolute
    top: 0
    left: 0
    color: #ffffff
</style>



