<template>
  <div>
    <div
      class="item"
      v-for="(item, index) of list"
      :key="index"
    >
      <div class="item-title border-bottom">
        <span class="item-title-icon"></span>
        {{item.title}}
      </div>
      <div v-if="item.children" class="item-title-children">
        <DetailList :list='item.children'></DetailList>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'DetailList',
  props: {
    list: Array
  }
}
</script>
<style scoped lang='stylus'>
.item-title-icon
  display: inline-block;
  position: relative;
  width: .36rem;
  height: .36rem;
  top: .06rem;
  left: .06rem;
  background: url(http://s.qunarzz.com/piao/image/touch/sight/detail.png) 0 -.45rem no-repeat;
  margin-right: .1rem;
  background-size: .4rem 3rem;
.item-title
  line-height: .8rem
  padding:0 0.2rem
  font-size: .32rem
.item-title-children
  padding:0 0.2rem
</style>



