<template>
  <div class="container" @click="handelGallaryClick">
    <div class="wraper">
      <swiper :options="swiperOption" >
        <swiper-slide v-for="(item, index) of imgs" :key="index">
          <img class="gallary-img" :src="item" alt="">
        </swiper-slide>
        <div class="swiper-pagination"  slot="pagination"></div>
      </swiper>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CommonGallary',
  props: {
    imgs: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      swiperOption: {
        pagination: '.swiper-pagination',
        paginationType: 'fraction',
        observer: true,
        observeParents: true
      }
    }
  },
  methods: {
    handelGallaryClick () {
      this.$emit('close')
    }
  }
}
</script>
<style scoped lang='stylus'>
.container >>> .swiper-container
  overflow:inherit
.container
  z-index:99
  position:fixed
  top:0
  left:0
  bottom:0
  right:0
  background:#000
  display:flex
  flex-direction:column
  justify-content:center
  .wraper
    height:0
    padding-bottom:100%
    width:100%
    .gallary-img
      width:100%
  .swiper-pagination
    color:#fff
  .swiper-pagination-fraction
    bottom: -1rem
</style>



