<template>
  <div class="city">
    <CityHeader></CityHeader>
    <CitySearch :cities='cities'></CitySearch>
    <CityList
      :cities='cities'
      :hotCities='hotCities'
      :letter='letter'
      ></CityList>
    <CityAlphabet
      :cities='cities'
      @change = 'handelLetterChange'
    ></CityAlphabet>
  </div>
</template>
<script>
import axios from 'axios'
import CityHeader from './components/Header'
import CitySearch from './components/Search'
import CityList from './components/List'
import CityAlphabet from './components/Alphabet'
export default {
  name: 'City',
  components: {
    CityHeader,
    CitySearch,
    CityList,
    CityAlphabet
  },
  data () {
    return {
      cities: {},
      hotCities: [],
      letter: ''
    }
  },
  methods: {
    getCityInfo () {
      axios.get('api/city.json').then(
        this.handlerCitySucc
      )
    },
    handlerCitySucc (res) {
      res = res.data
      if (res && res.data) {
        const data = res.data
        this.cities = data.cities
        this.hotCities = data.hotCities
      }
    },
    handelLetterChange (letter) {
      this.letter = letter
    }
  },
  mounted () {
    this.getCityInfo()
  }
}
</script>
<style scoped lang='stylus'>

</style>



