<template>
  <div class="list" ref="wrapper">
    <div>
        <div class="area border-topbottom">
        <div class="title">当前城市</div>
        <div class="button-list">
          <div class="button-wrap">
            <div class="button">{{this.currentCity}}</div>
          </div>
        </div>
      </div>
      <div class="area border-topbottom">
        <div class="title">热门城市</div>
        <div class="button-list">
          <div class="button-wrap"
            v-for="item of hotCities"
            :key="item.id"
            @click="handelCityClick(item.name)"
          >
            <div class="button">{{item.name}}</div>
          </div>
        </div>
      </div>
      <div class="area"
        v-for="(item, key) of cities"
        :key="key"
        :ref="key"
      >
        <div class="title border-topbottom">{{key}}</div>
        <div class="item-list">
          <div class="item border-bottom"
            v-for="innerItem of item"
            :key="innerItem.id"
            @click="handelCityClick(innerItem.name)"
          >{{innerItem.name}}</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'CityList',
  props: {
    cities: Object,
    hotCities: Array,
    letter: String
  },
  computed: {
    ...mapState({
      currentCity: 'city'
    })
  },
  mounted () {
    const wrapper = this.$refs.wrapper
    this.scroll = new BScroll(wrapper)
  },
  methods: {
    handelCityClick (city) {
      // this.$store.commit('changCity', city)
      this.changCity(city)
      this.$router.push('/')
    },
    ...mapMutations(['changCity'])
  },
  watch: {
    letter () {
      if (this.letter) {
        const el = this.$refs[this.letter][0]
        this.scroll.scrollToElement(el)
      }
    }
  }
}
</script>
<style scoped lang='stylus'>
@import '~styles/varibles.styl'
.border-topbottom
  &:before
    border-color: #cccccc
  &:after
    border-color: #cccccc
.border-bottom
  &:before
    border-color: #cccccc
.list
  overflow: hidden
  position: absolute
  top: 1.78rem
  left: 0
  right: 0
  bottom: 0
  .title
    line-height: 0.55rem
    background: #eeeeee
    padding-left: .2rem
    color: #666666
    font-size: 0.26rem
  .button-list
    overflow: hidden
    padding 0.1rem 0.3rem 0.1rem 0.1rem
    .button-wrap
      width:33.33%
      float: left
      .button
        margin: 0.1rem
        padding:0.1rem 0
        text-align: center
        border: 0.02rem solid #cccccc
        border-radius: 0.06rem
  .item-list
    .item
      line-height:0.76rem
      color: #666
      padding-left: 0.2rem
</style>



